/* eslint-env browser */
/* eslint react/prop-types: 0 */
import React, { Component } from 'react';
import Form from './components/FormComponent';
import { fields } from './components/formconfig';
import './css/app.css';
import ResultList from './components/ResultListComponent';
import SavedStateList from './components/SavedStateComponents';
import calculations from './logic/calculations';

const apiURL = 'http://192.168.0.12:3000/comparisons';

class App extends Component {
  static findState(savedStates, stateId) {
    return savedStates.Items.find(state => state.id === stateId);
  }

  static setDefaultFormData() {
    const defaults = {};
    // const { state } = this;
    fields.map((ob) => {
      defaults[ob.name] = ob.defVal;
      return false;
    });
    return defaults;
  }

  static calc(inputData) {
    const dealFinance = calculations.initialFinance(inputData);
    const buyToLet = calculations.freeCash(inputData);
    const flip = calculations.flip(inputData);
    const stress = calculations.stressTest(inputData);
    const currSymbol = calculations.getCurrencyCode(inputData);

    return {
      data: {
        dealFinance,
        buyToLet,
        stress,
        flip,
      },
      currency: currSymbol,
    };
  }

  constructor(props) {
    super(props);
    this.state = {
      data: {
        dealFinance: [],
        buyToLet: [],
        stress: [],
        flip: [],
      },
      fields,
      currency: 163,
      savedStates: { Items: [] },
      selectedState: App.setDefaultFormData(),
    };
    this.doResults = this.doResults.bind(this);
    this.calculate = this.calculate.bind(this);
    // this.setDefaultFormData = this.setDefaultFormData.bind(this);
    this.saveState = this.saveState.bind(this);
    this.deleteState = this.deleteState.bind(this);
  }

  componentDidMount() {
    const { selectedStateId } = this.props;
    // console.log(selectedStateId);
    this.getSavedStates(selectedStateId || '');
  }

  static getDerivedStateFromProps({ selectedStateId }, { savedStates }) {
    const newLoadedState = App.findState(savedStates, selectedStateId) || App.setDefaultFormData();
    if (savedStates.Items.length > 0) {
      const { data, currency } = App.calc(newLoadedState);
      return {
        data,
        currency,
        selectedState: newLoadedState,
      };
    }
    return null;
  }

  getSavedStates(selectedStateId) {
    fetch(apiURL)
      .then(response => response.json())
      .then((responseData) => {
        let formData;
        if (selectedStateId !== '') {
          formData = App.findState(responseData, selectedStateId);
        } else {
          formData = App.setDefaultFormData();
        }
        const { data, currency } = App.calc(formData);
        this.setState({
          data,
          currency,
          savedStates: responseData,
          selectedState: formData,
        });
      })
      .catch(() => {
        const formData = App.setDefaultFormData();
        const { data, currency } = App.calc(formData);
        this.setState({
          data,
          currency,
          savedStates: { Items: 'No saved items avaialable' },
          selectedState: formData,
        });
      });
  }

  calculate(inputData) {
    this.setState(App.calc(inputData));
  }

  doResults() {
    const { data } = this.state;
    const {
      dealFinance,
      buyToLet,
      stress,
      flip,
    } = data;

    return (
      <React.Fragment>
        <div className="res-block">
          <h4>Deal finance</h4>
          <ResultList id="1" data={dealFinance} />
        </div>
        <div className="res-block">
          <h4>Buy to let</h4>
          <ResultList id="2" data={buyToLet} />
        </div>
        <div className="res-block">
          <h4>Stress test</h4>
          <ResultList id="3" data={stress} />
        </div>
        <div className="res-block">
          <h4>Flip</h4>
          <ResultList id="4" data={flip} />
        </div>
      </React.Fragment>
    );
  }

  saveState(formData) {
    fetch(apiURL, {
      method: 'PUT',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(formData),
    })
      .then(response => response.json())
      .then(() => {
        this.getSavedStates();
      }).catch(() => this.setState({ savedStates: { Items: 'No save service avaialable' } }));
  }

  deleteState(stateId) {
    fetch(apiURL, {
      method: 'DELETE',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ id: stateId }),
    })
      .then((resp) => {
        if (resp.ok) {
          this.getSavedStates();
        } else {
          throw new Error('delete failed');
        }
      }).catch(() => this.setState({ savedStates: { Items: 'No delete service avaialable' } }));
  }

  render() {
    const { currency, savedStates, selectedState } = this.state;
    const { Items } = savedStates;
    const savedStateList = (
      <div className="column states">
        <SavedStateList
          data={Items}
          ondelete={this.deleteState}
        />
      </div>
    );
    const savedList = Items ? savedStateList : '';

    return (
      <div className="App">
        <div className="column">
          <Form
            name="propcalc"
            fields={fields}
            formData={selectedState}
            twocols="yes"
            calculate={this.calculate}
            currsymbol={currency}
            onsave={this.saveState}
          />
        </div>
        <div className="column results">
          {this.doResults()}
        </div>
        {savedList}
      </div>
    );
  }
}

export default App;
