// eslint-disable-next-line
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import App from '../App';

const handleDelete = (e, callback) => {
  e.preventDefault();
  callback(e.target.id);
};

const SavedStateListItem = ({
  label,
  value,
  ondelete,
}) => (
  <li>
    <div className="state">
      <Link
        className="btn-sm btn-primary"
        style={{ width: '100%' }}
        to={value}
      >
        {label}
      </Link>
    </div>
    <div className="remove-state">
      <button
        className="btn-sm btn-primary btn-warning"
        type="submit"
        id={value}
        onClick={e => handleDelete(e, ondelete)}
      >
        delete
      </button>
    </div>
  </li>
);

const SavedStateList = ({ data, ondelete, onselect }) => {
  if (data.map) {
    return (
      <ul>
        {data.map(({ projectName, id }, ind) => {
          const key = `li-${id}-${ind}`;
          return (
            <SavedStateListItem
              ondelete={ondelete}
              key={`li-${key}`}
              label={projectName || 'untitled'}
              value={id}
              onselect={onselect}
            />
          );
        })}
        <li>
          <div className="state" style={{ width: '100%' }}>
            <Link
              className="btn-sm btn-primary"
              style={{ width: '100%' }}
              to="/"
            >
              create new
            </Link>
          </div>
        </li>
      </ul>
    );
  }
  return '';
};

export default SavedStateList;
